table {
  margin-left: 20px;
  border: 1px solid #000;

  th {
    border: 1px solid #000;
  }

  tr {
    td {
      border: 1px solid #000;
    }
  }
}

.manual-box {
  padding: 24px;
  .title {
    margin-top: 24px;
    font-size: 32px;
    font-weight: bold;
  }

  .step {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 24px;

    input[type="text"] {
      width: 320px;
    }

    span {
      cursor: pointer;
      border-bottom: 1px solid #000;
    }


    .step-title {
      margin-bottom: 12px;
      font-size: 20px;
      font-weight: 600;
    }

    .position-box {
      margin-top: 24px;
    }
  }
}